<template>
  <div class="title">
    <div id="wrapper">
      <vue-particles
        color="#587792"
        :particleOpacity="1.0"
        :particlesNumber="40"
        shapeType="circle"
        :particleSize="9"
        linesColor="8db1ab"
        :linesWidth="2"
        :lineLinked="true"
        :lineOpacity="1.0"
        :linesDistance="170"
        :moveSpeed="0.75"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
      ></vue-particles>
      <div id="wrapper">
        <div id="text">
          <div id="first">Vishnu</div>
          <div id="last">Rengaraj</div>
          <div id="subtitle">{{ whois[0] }}</div>
        </div>
      </div>

      <!-- <router-link :to="{ name: 'Home2About' }"> -->
      <div id="open-about-text" :style="dynamicStyles">learn more</div>
      <div id="open-about-button" :style="dynamicStyles">
        <img src="../assets/chevron_down.png" />
      </div>
      <!-- </router-link> -->
    </div>
    <div id="contents">
      <About />
      <!-- <Project /> -->
    </div>
  </div>
</template>

<script>
// v-on:wheel.passive="wheelson"
// v-touch:swipe.top="swipeUpHandler"
// import Resume from "./Resume.vue";
// import Project from "./Project.vue";
// var VueScrollTo = require("vue-scrollto");
import About from "./About.vue";
// import Project from "./Project.vue";

export default {
  name: "title",
  components: {
    About,
    // Project,
  },

  data() {
    return {
      scrollPosition: 0,
      windowHeight: window.innerHeight,
      scrollRatio: 1.0,
      // isActiveProject: false,
      whois: [
        "Mechanical Engineer",
        "Computer Scientist",
        "Graduate Student",
        "Designer",
        "Stress Analyst",
        "Banana Lover",
      ],
    };
  },
  mounted() {
    window.addEventListener("scroll", this.updateScroll);

    window.addEventListener("resize", () => {
      this.windowHeight = window.innerHeight;
    });

    window.setInterval(() => {
      this.pollPerson();
    }, 2700);
  },
  methods: {
    updateScroll() {
      this.scrollPosition = window.scrollY;
      this.scrollRatio = 1.0 - 3.0 * (this.scrollPosition / this.windowHeight);
      // console.log(this.scrollRatio);
    },

    pollPerson() {
      const first = this.whois.shift();
      this.whois = this.whois.concat(first);
    },
  },
  computed: {
    dynamicStyles() {
      return {
        "--opac": this.scrollRatio,
      };
    },
  },
};
</script>

<style scoped>
