<template>
  <div class="project-card">
    <!-- P1 -->
    <div>
      <md-card md-with-hover>
        <md-card-media md-big v-if="windowWidth < 750">
          <div v-if="!isVideo">
            <img :src="imageURL" :alt="imageDescription" />
          </div>
          <div v-if="isVideo">
            <video autoplay loop muted playsinline>
              <source
                :src="imageURL"
                :alt="imageDescription"
                type="video/mp4"
              />
            </video>
          </div>
        </md-card-media>

        <md-card-header>
          <md-card-header-text>
            <div class="md-title">{{ title }}</div>
            <div class="md-subhead">{{ subtitle }}</div>
            <md-card-content v-if="windowWidth >= 750">
              {{ body }}
            </md-card-content>
          </md-card-header-text>

          <md-card-media md-big v-if="windowWidth >= 750">
            <div v-if="!isVideo">
              <img :src="imageURL" :alt="imageDescription" />
            </div>
            <div v-if="isVideo">
              <video autoplay loop muted playsinline>
                <source
                  :src="imageURL"
                  :alt="imageDescription"
                  type="video/mp4"
                />
              </video>
            </div>
          </md-card-media>
        </md-card-header>

        <md-card-actions v-if="windowWidth >= 750">
          <md-button class="pill" :href="buttonLink" target="_blank"
            ><strong>{{ buttonName }}</strong></md-button
          >
        </md-card-actions>
        <md-card-expand v-if="windowWidth < 750">
          <md-card-actions md-alignment="space-between">
            <div>
              <md-button class="pill" :href="buttonLink" target="_blank"
                ><strong>{{ buttonName }}</strong></md-button
              >
            </div>

            <md-card-expand-trigger>
              <md-button class="md-icon-button">
                <strong><md-icon>keyboard_arrow_down</md-icon></strong>
              </md-button>
            </md-card-expand-trigger>
          </md-card-actions>

          <md-card-expand-content>
            <md-card-content>
              {{ body }}
            </md-card-content>
          </md-card-expand-content>
        </md-card-expand>
      </md-card>
    </div>
  </div>
</template>

<script>
export default {
  name: "project-card",
  props: [
    "imageURL",
    "imageDescription",
    "title",
    "subtitle",
    "body",
    "buttonName",
    "buttonLink",
    "isVideo",
  ],

  components: {},

  data() {
    return {
      windowWidth: window.innerWidth,
    };
  },

  mounted() {
    window.addEventListener("resize", () => {
      this.windowWidth = window.innerWidth;
    });
  },

  methods: {},
};
</script>

<style scoped>
.pill {
  color: #eeeeee !important;
  background-color: black;
  border-radius: 18px;
}
.project-card {
  text-align: left;
  transition: all 0.75s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.md-card-media img {
  margin: 5px;
  transition: all 0.75s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.md-card {
  margin: 15px;
  margin-left: 2px;
  margin-right: 2px;
  border-radius: 15px;
  border: 1px solid #eeeeee;
  cursor: default;
}

.md-title,
.md-subhead {
  font-weight: 600;
}

.md-title {
  font-size: 30px;
}

.md-subhead {
  font-size: 16px;
  padding-top: 6px;
}

.md-card-header .md-card-media.md-big {
  max-width: 480px;
  max-height: 480px;
  flex: 0 1 480px;

  transition: all 0.75s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.md-card.md-theme-default .md-card-expand .md-card-actions {
  border-bottom-left-radius: 14px;
  border-bottom-right-radius: 14px;
}

@media screen and (max-width: 900px) {
  .md-card-header .md-card-media.md-big {
    max-width: 320px;
    max-height: 320px;
    flex: 0 1 320px;
  }
}

@media screen and (max-width: 750px) {
  .md-card-media img,
  .md-card-media video {
    border-top-left-radius: 14px;
    border-top-right-radius: 14px;
    /* border: 1px solid #eeeeee; */
    margin: 0px;
  }
  .md-card {
    margin: 4px;
    margin-top: 20px;

    border-radius: 14px;
    /* background-color: white; */
    border: 0px solid #eeeeee;
  }
}

@media screen and (max-width: 500px) {

  .md-title {
    font-size: 24px;
  }

  .md-subhead {
    font-size: 14px;
    padding-top: 6px;
  }
}
</style>